import React from 'react';
import logo from '../../logo.svg';
import '../../assets/css/Main.css';
import { Navbar, NavbarBrand } from 'reactstrap';
import Menu from './menu';
import { PLACES } from '../../assets/data/places';
import Greet from './greet';

class Main extends React.Component {					
  
	constructor(props){
		super(props);
		
		this.state = {
			places: PLACES
		};
	}
	
	render(){
		return (
			<div className='Main'>
				<Navbar color="primary">
					<div className="container" id="brand">
						<NavbarBrand>Tanuj Gupta- Graduate Student At UCSC</NavbarBrand>
					</div>
				</Navbar>
				<h1>About Me</h1>
				<p>I love to solve real-life engineering problems. I am particularly interested in building distributed systems that are scalable and have low latency and high throughput. In anticipation of an opportunity in an organization providing intellectually challenging work to improve and enrich my knowledge and skills to grow along with the organization.</p>
				<h1>Cources taken</h1>
				<Menu places={this.state.places} />
			</div>
		);
	}
}

export default Main;